import { Component, OnDestroy, OnInit } from '@angular/core';
import { CoreRoutes, RoomsPageName } from "@core/configs/routing-config";
import { AnimationBuilder } from "@angular/animations";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { GuestService } from "@core/services/guest-service/guest.service";
import { IGuest } from "@shared/models/guest.model";
import { HotelService } from "@core/services/hotel-service/hotel.service";
import { IHotel } from "@shared/models/hotel.model";
import { SelectionService } from "@core/services/selection-service/selection.service";
import { takeUntil } from "rxjs/operators";
import { Subject } from "rxjs";
import { DataService } from "@core/services/data-service/data.service";
import { IPagesData } from "@shared/models/pages-data.model";
import { IReservation } from "@shared/models/reservation.model";
import { ActivatedRoute, Router } from "@angular/router";
import { LocalStorageService } from "@core/services/store-service/store.service";

@Component({
  selector: 'app-introduction-page',
  templateUrl: './introduction-page.component.html',
  styleUrls: ['./introduction-page.component.scss']
})
export class IntroductionPageComponent extends AbstractAnimatedComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();

  public readonly selectionsTitleCountToShow: number = 2;
  public roomsPageName = RoomsPageName;
  public coreRoutes = CoreRoutes;

  pagesData: IPagesData = {} as IPagesData;
  hotelData: IHotel = {} as IHotel;
  guestData: IGuest = {} as IGuest;
  reservationData: IReservation = {} as IReservation;

  constructor(
      private builder: AnimationBuilder,
      private route: ActivatedRoute,
      private router: Router,
      private _dataService: DataService,
      private _guestService: GuestService,
      private _hotelService: HotelService,
      private _selectionService: SelectionService,
      private _localStoreService: LocalStorageService,
  ) {
    super(builder);
  }

  ngOnInit(): void {
    this.subscribes();
  }

  subscribes(): void {
    let { hash } = this.route.snapshot.queryParams;
    if (!hash) {
      this.router.navigate([this.coreRoutes.LINK_NOT_VALID])
    }
    this._localStoreService.setItem('hash', hash);
    this._dataService.fetchPagesData(hash) //87585a765b8768c
        .pipe(
            takeUntil(this.destroy$)
        )
        .subscribe(val => {
          this._dataService.pagesData = this.pagesData = val;

          this._selectionService.selectionPages = val.selectionPages;
          this._hotelService.hotelData = this.hotelData = val.hotel;
          this._guestService.guestData = this.guestData = val.guest;
          this._guestService.guestReservationData = this.reservationData = val.reservation;
        });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
